.container {
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1320px; }
  @media (min-width: 420px) {
    .container {
      max-width: 1350px; } }
  @media (min-width: 720px) {
    .container {
      padding-left: 50px;
      padding-right: 50px;
      max-width: 1390px; } }
  @media (min-width: 1200px) {
    .container {
      padding-left: 70px;
      padding-right: 70px;
      max-width: 1430px; } }



